.bg {
  background-image: url("https://i.pinimg.com/originals/9e/45/7f/9e457f19eabaa71335b2b5fb8a74830f.jpg");
  margin: 0px;
  display: flex;
  position: relative;
}

.text {
  margin: auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
  opacity: 0.7;
  color: purple;
  background-color: pink;
  padding: 30px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 700;
  font-size: 20px;
  border: 1px solid #666;
  border-radius: 5px;
  max-width: 30%;
  text-align: center;
}

.para {
  padding: 1rem;
}
.input-label {
  display: block;
}
.input {
  font-size: 15px;
  padding: 1rem 1rem;
  margin-bottom: 1rem;
  border: 1px hidden;
  border-radius: 0.5rem;
  width: 100%;
  max-width: 200px;
}
.button {
  margin: 1rem 1rem;
  padding: 0.5rem 1rem;
  border: 2px solid black;
  background-color: transparent;
  border-radius: 1rem;
  font-size: larger;
  cursor: pointer;
}
.message {
  font-size: larger;
  font-weight: bold;
  padding-bottom: 1rem;
}
